.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
  .App{
    font-family: "Roboto", Arial, sans-serif !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    align-items: center;
  }
  
  .calculator{
    margin: 5px;
  }
  .display {
    outline: none;
    border: none;
    width: 310px;
    height: 100px;
    font-size: 2.5rem;
    font-weight: 100;
    padding: 2px 15px;
    text-align: right;
    color: white;
    background: #1c191c;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }
  
  button {
    width: 85px;
    height: 95px;
    font-size: 25px;
    font-weight: 100;
    cursor: pointer;
    border-top: 1px solid #666;
    border-left: none;
    border-bottom: none;
  }
  
  .digit-keys {
    background-color: #E0E0E7;
    border-right: 1px solid #666;
  }
  
  .fun-keys {
    background-color: #C9C8CC;
    border-right: 1px solid #666;
  }
  
  .operator-keys {
    color: #fff;
    border-right: none;
    background-color: #5142FF;
  }
  
  .digit-keys:hover {
    background-color: #d9d9d9;
  }
  
  .fun-keys:hover {
    background-color: #aba9a9;
  }
  
  .operator-keys:hover {
    background-color: #3021d7;
  }
  .bottom-right-corner{
    border-bottom-right-radius: 6px;
  }
  .bottom-left-corner{
    width: 255px;
    border-bottom-left-radius: 6px;
  }